@charset "utf-8";
* {
  box-sizing: border-box;
  font-family: "Noto Sans KR", "Roboto";
}
html, body, div, span, applet, object, iframe,
/* h1, h2, h3, h4, h5, h6, */p, blockquote, pre, 
a, abbr, acronym, address, big, cite, code,
del, dfn, em, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, /*button,*/ input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-family: "Noto Sans KR", "Roboto";
  vertical-align: middle;
  color: #111111;
  font-weight: 400;
}
img {
  vertical-align: middle;
  margin: 0;
  padding: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  position: relative;
}
a {
  color: #111111;
  text-decoration: none;
}
a:hover {
  color: #005d75;
}
/* button{
	border: none;
	background-color: transparent;
} */
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* 인풋 디자인 통일 */
input,
textarea {
  border: 1px solid #cccccc;
}
input[type="text"],
input[type="number"],
input[type="password"],
input[type="file"],
input[type="email"],
input[type="image"],
input[type="range"],
input[type="time"],
input[type="file"],
textarea {
  padding: 5px;
  width: 100%;
  outline: none;
}

select {
  border: 1px solid #cccccc;
  outline: none;
  padding: 5px 10px;
}
input:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #06adc3;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox  */
input[type="number"] {
  -moz-appearance: textfield;
}

@media screen and (max-width: 480px) {
  input[type="text"],
  input[type="password"] {
    padding: 5px;
    width: 100%;
    outline: none;
    font-size: 0.8rem;
  }
}
