.gallery{display:grid;grid-template-columns:repeat(6, 1fr);gap:20px;padding:5px}.gallery .image-center{display:flex;justify-content:center;align-items:center}.gallery .gallery-item{background-color:#f4f4f4;padding:10px;border-radius:8px;box-shadow:0 4px 8px rgba(0, 0, 0, 0.1);display:flex;flex-direction:column;justify-content:space-between;align-items:left}.gallery .gallery-item .item-image{width:90%;object-fit:cover}.gallery .gallery-item .item-content h3{margin:0;font-size:18px;color:#333}.gallery .gallery-item .item-content p{margin:5px 0;color:#666}.gallery .gallery-item .item-content .status{font-weight:bold;color:#d9534f}.promotion-date-check{display:flex;align-items:center;padding:20px;border:1px solid #ccc;margin-bottom:10px}.promotion-date-check .promotion-date-search .promotion-date-search-btn{width:100px;padding:7px 0;display:block;background-color:#06adc3;color:#fff;text-align:center;font-size:.8rem;font-weight:500;cursor:pointer}.promotion-date-check .promotion-date-search .promotion-date-search-btn:hover{background-color:#005d75}.promotion-date-check .promotion-date-search .promotion-date-search-btn::after{content:"";font-family:FontAwesome;font-size:1rem;width:24px;height:24px;text-align:center;color:#fff;margin:0 5px}.promotion-date-check .promotion-period-text{font-size:.8rem;margin-right:10px}.write-btn{margin:5px}